import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import {config} from '../../app.config';
import {NgForm} from "@angular/forms";

declare var $: any;

export interface ClientData {
  id?: number;
  name: string;
  email: string;
  phone_number: string;
  password: string;
  createdby: number;
  creation_time: string;
  last_modify_by: number;
  last_modify_time: string;
  password_lastmodify: string;
  images_iid: number;
  lon: number;
  lat: number;
}

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css']
})
export class ClientComponent implements OnInit {
  model: ClientData;
  editMode: boolean;
  clientObserver = this.http.get(config.api_prefix + 'client').map(resp => resp.json().result);
  imageObserver =  this.http.get(config.api_prefix + 'utils/image').map(resp => resp.json().result);
  clientsList;

  resetModel() {
    this.model = {
      name: '',
      email: '',
      phone_number: '',
      password: '',
      createdby: 0,
      creation_time: '',
      last_modify_by: 0,
      last_modify_time: '',
      password_lastmodify: '',
      images_iid: 0,
      lon: 0,
      lat: 0,
    };
  }

  constructor(private http: Http) { }

  ngOnInit() {
    this.updateClientsList();
    this.resetModel();
  }

  updateClientsList() {
    this.clientObserver.take(1).subscribe(
      (result) => {
        this.clientsList = result;
      }
    );
  }

  onNewClient() {
    console.log('new chef');
    $('#create-form').modal('toggle');
  }

  // todo save client
  onSave(form: NgForm) {
    console.log(this.model);
    let request = this.http.post(config.api_prefix + 'client/', this.model);
    if (this.editMode) {
      request = this.http.patch(config.api_prefix + 'client/', this.model);
      this.model.last_modify_time = new Date().toISOString();
    }  else {
      this.model.creation_time = new Date().toISOString();
      this.model.password_lastmodify = new Date().toISOString();
      this.model.last_modify_time = new Date().toISOString();
    }

    request.subscribe(
      (result) => {console.log('ok', result); this.updateClientsList() },
      (err) => console.log('err', err)
    );
    this.resetModel();
    form.reset();
    this.editMode = false;
  }

  onEdit(client) {
    this.editMode = true;
    console.log('on edit');
    this.model = client;
    $('#create-form').modal('toggle');
  }

  onDelete(id) {
    console.log('on delete', id);
    this.http.delete(config.api_prefix + 'client/' + id).subscribe(
      (result) => {console.log('ok', result); this.updateClientsList() },
      (err) => console.log('err', err)
    );
  }
}



